import{View} from'./View.js';

export class MensagemView extends View{  
    constructor(elemento){
        super(elemento);
    }
    
    template(model){
        return model.texto 
        ?`<p class="alert alert-info">${model.texto}</p>` //? é tipo o if
        :`<p></p>`;                         //: é tipo o else
    }
    
}